      </v-flex>

      <v-flex xs12 sm4 class="px-1">
        <label>Nh sn xut nc ngoi: <v-icon size="18" style="color: green;">{{vr_ApplicantProfile['markupOverseasManufacturer'] === 0 ? 'done' : 'done'}}</v-icon></label>
      </v-flex>

      <v-flex xs12 sm4 class="px-1">
        <label>C s sn xut trong nc: <v-icon size="18" style="color: green;">{{vr_ApplicantProfile['markupDomesticsManuacturer'] === 0 ? 'done' : 'done'}} </v-icon></label>
      </v-flex>

      <v-flex xs12 sm12>
        <table border="1" class="table-simple" style="width: 100%;" >
          <thead>
          </thead>

          <tbody>
            <tr>
